body {
  margin: 20px;
  font-family: Arial, sans-serif;
}

h1 {
  text-align: center;
}

#chat-container {
  border: 1px solid #ccc;
  border-radius: 5px;
  max-width: 600px;
  margin: 0 auto;
  padding: 10px;
}

.message {
  background-color: #f2f2f2;
  border-radius: 5px;
  width: 75%;
  margin: 5px 0;
  padding: 10px;
}

.human {
  text-align: right;
  float: right;
  background-color: #e2f0fd;
}

.ai {
  text-align: left;
  float: left;
  background-color: #f5e6ce;
}

#user-input {
  margin-top: 20px;
}

#user-input-box {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 80%;
  padding: 10px;
}

#send-button {
  color: #fff;
  cursor: pointer;
  float: right;
  border: none;
  border-radius: 5px;
  padding: 10px 15px;
}

#send-button.enabled {
  background-color: #007bff;
}

#send-button.enabled:hover {
  background-color: #0056b3;
}

#send-button.disabled {
  background-color: #666;
}

#header {
  text-align: center;
  margin-bottom: 20px;
}

#logo {
  max-width: 100px;
  margin: 0 auto 10px;
  display: block;
}

#disclaimer-container {
  text-align: center;
  max-width: 600px;
  margin: 20px auto 0;
}

#disclaimer {
  color: #666;
  font-style: italic;
}

ul.sources {
  padding: 0;
  list-style: none;
}

